<template>
  <div class="chat">
    <van-nav-bar fixed title="" left-text="" left-arrow @click-left="$router.back()"></van-nav-bar>

    <div class="chat-window" ref="chat-window">
      <van-cell :class="item.from!=='myself'?'left':'right'" v-for="(item,index) in msgs" :key="index" :value="item.msg" />
      <div class="clear"></div>
    </div>

    <van-cell-group border class="sendmsg">
      <van-field border v-model="msg" center>
        <van-button slot="button" size="small" type="primary"
          @click="sendMsg"
          :disabled="isConnect||!msg.length"
         ></van-button>
      </van-field>
    </van-cell-group>
  </div>
</template>

<script>
import io from 'socket.io-client'
export default {
  name: 'Chat',
  components: {},
  data () {
    return {
      msg: ',',
      msgs: [],
      socket: null, // 
      isConnect: false // false
    }
  },
  watch: {},
  methods: {
    // 
    sendMsg () {
      // 4. 
      this.setSocket()
    },
    // 2. 
    sendMsgToRobat () {
      this.socket.emit('message', {
        msg: this.msg,
        timestamp: Date.now()
      })
      // 
      this.msgs.push({
        msg: this.msg,
        from: 'myself' // ,son
      })
      this.msg = ''
      this.recMsgFromRobat()
    },
    // 3. 
    recMsgFromRobat () {
      this.socket.on('message', (data) => {
        // console.log('--->', data)
        this.msgs.push({
          msg: data.msg,
          from: 'yourself' // ,son
        })
      })
      this.isConnect = false
    },
    // 1. 
    setSocket () {
      // 
      this.socket = io('http://ttapi.research.itcast.cn', {
        query: {
          token: this.$store.state.user.token
        }
      })
      this.socket.on('connect', () => {
        console.log('') // ,
        this.isConnect = true
        this.sendMsgToRobat()
      })
    }
  },
  created () {
    if (this.$store.state.user) {
      this.setSocket()
    }
  }
}
</script>

<style lang='less' scoped>
.van-nav-bar {
  background-color: #1989fa;
  .van-nav-bar__title {
    color: #ffffff;
  }
}

.sendmsg {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
.sendmsg /deep/ .van-cell {
  border: 1px solid black;
}

.chat .chat-window {
  position: relative;
  top: 92px;
  font-size: 14px;
}
.chat /deep/ .van-nav-bar__text {
  color: #ffffff;
}
.clear {
  height: 102px;
}
.chat-window /deep/ .left .van-cell__value {
  color: red;
}
.chat-window /deep/ .right .van-cell__value {
  color: green;
}
</style>
